import Loading from '../../public/Loading'
  export default {
      name: "refund",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          index:1,
          value:1,
          house:[],
          options:[
            {label:'全退',value:1},
            {label:'非全退',value:2},
          ],
          reson:'',
          orderInformation:'',
          personNum:1,
          pay_refund:'',
          balance:'',
          total_price:"",
          order_id:'',
          isLoading:false,
          returnFace:'',
          disabled:false
        }
    },
    components:{
      Loading,
    },
    watch:{
      value:function(){
        if(this.value == 1){
            this.getAmount(this.order_id,'','','',1)
        }else{
          let b= []
          for(let i =0;i<this.orderInformation.house.length;i++){
            b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].push.number})
          }
          this.getAmount(this.order_id,this.personNum,JSON.stringify(b),this.reason,0)
        }
      },
      personNum:function(){
        let b= []
        for(let i =0;i<this.orderInformation.house.length;i++){
          b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].push.number})
        }
        this.getAmount(this.order_id,this.personNum,JSON.stringify(b),this.reason,0)
      },

    },
    methods:{
      goBack(){
        if(this.index == 2){
          this.$router.push('/transaction')
        }else{
          this.$router.go(-1)
        }
      },
      reduceP(val){
        if(val == 1){
          this.personNum -= 1
        }else{
          this.personNum += 1
        }
      },
      reduceH(item,val){
        if(val == 1){
          item.push.number -= 1
          let b= []
          for(let i =0;i<this.orderInformation.house.length;i++){
            b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].push.number})
          }
          this.getAmount(this.order_id,this.personNum,JSON.stringify(b),this.reason,0)
        }else{
          item.push.number += 1
          let b= []
          for(let i =0;i<this.orderInformation.house.length;i++){
            b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].push.number})
          }
          this.getAmount(this.order_id,this.personNum,JSON.stringify(b),this.reason,0)
        }
      },
      sendRefun(){
        this.isLoading = true
        if(this.value == 1){
          this.$http.post(this.api + '/RefundS',{
            token: localStorage.getItem('token'),
            order_id: this.order_id,
            flag: 1,
            reason: this.reson,
            type: 0
          })
          .then(res=>{
            if(res.data.code == 1){
              this.isLoading = false
              if(this.pay_refund){
                this.index = 2
              }else{
                this.$router.push('/refundOrder')
                this.$message({
                  type:'success',
                  message:'您的退款将会根据该活动退款政策在2-3个工作日进行退款！'
                })
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.sendRefun()
              })
            }else if(res.data.code == 0){
              this.isLoading = false
              alert(res.data.msg)
            }
          })
        }else{
          console.log(8)
            let b = []
            for(let i =0;i<this.orderInformation.house.length;i++){
              b.push({oh_id:this.orderInformation.house[i].oh_id,num:this.orderInformation.house[i].push.number})
            }
           this.$http.post(this.api + '/RefundS',{
            token: localStorage.getItem('token'),
            order_id: this.order_id,
            flag: 0,
            type:0,
            person_num: this.personNum,
            reason: this.reson,
            house:JSON.stringify(b),
          })
          .then(res=>{
            if(res.data.code == 1){
              console.log(9)
              this.isLoading = false
               if(this.pay_refund){
                 this.index = 2
              }else{
                 this.$message({
                   type:'info',
                   message:'您的退款将会根据该活动退款政策在2-3个工作日进行退款！'
                 })
                 this.$router.push('/refundOrder')
               }
            }else if(res.data.code == 3){
               this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.sendRefun()
              })
            }else if(res.data.code == 0){
              this.isLoading = false
              alert(res.data.msg)
            }
          })
        }
      },
      checkRefund(){
        this.$http.post(this.api + '/CheckPolicy',{
          token: localStorage.getItem('token'),
          order_id: this.order_id,
        })
        .then(res=>{
          if(res.data.code == 1){
            this.pay_refund = 1
          }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.checkRefund()
              })
          }else{
            this.pay_refund = 0
          }
        })
      },
      getAmount(order,person,house,reason,flag){
        this.$http.post(this.api + '/RefundAmount',{
          token: localStorage.getItem('token'),
          order_id:order,
          person_num:person,
          house:house,
          reason:reason,
          flag:flag
        })
          .then(res=>{
            if(res.data.code == 1){
              this.balance = res.data.data.balance
              this.total_price = res.data.data.pay_price
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getAmount(order,person,house,reason,flag)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getOrderMation(){
        this.$http.post(this.api + '/OrderD',{
          token: localStorage.getItem('token'),
          order_id: this.order_id
        })
          .then(res=>{
            if(res.data.code == 1){

              let data  = res.data.data
              for(let i =0;i<data.house.length;i++){
                data.house[i].push = ({number:1})
              }
              this.orderInformation = data
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOrderMation()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
    },
    mounted(){
        this.getOrderMation()
      this.checkRefund()
      this.getAmount(this.order_id,'','','',1)
    },
    created(){
      this.order_id = this.$route.query.information
      this.returnFace = this.$route.query.returnFace
      if(this.returnFace){
        this.disabled = true
        this.value = 2
      }else{
        this.disabled = false
        this.value = 1
      }
    }
  }



